@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Balsamiq+Sans:ital@0;1&family=Quicksand:wght@500&display=swap");

* {
  margin: 0;
  font-family: "Poppins";
}

@media only screen and (min-width: 767px) {
  * {
    cursor: none;
  }
  svg {
    cursor: unset;
  }
  path {
    cursor: unset;
  }
  a {
    cursor: unset;
  }
}
